/* 
 * google font
 */
@import url("https://fonts.googleapis.com/css?family=Lato:400,400i|Tienne:400,700");
p, ol, ul {
  margin: 0;
}
.cv-container__app {
  padding-top: 20px;
  padding-bottom: 20px;
}
.cv-container__list ol {
  padding-left: 2rem;
}
.cv-decorator__font-title {
  font-family: "Lato", sans-serif;
}
.cv-decorator__font-fam {
  font-family: "Tienne", serif;
  font-size: 1.2rem;
}
.cv-decorator__padding-y {
  padding: 1rem 0;
}
.cv-decorator__padding-top {
  padding-top: 1rem;
}
.cv-decorator__padding-bottom {
  padding-bottom: 1rem;
}
.cv-decorator__border-bottom {
  border-bottom: 1px dashed #8D8D8E;
}
.cv-item {
  margin: 0 4px;
  text-decoration: underline;
}



